.c-btn-slide {
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100px;
  padding-left: 2em;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  color: #999;
  border: 1px solid #999;
  opacity: 1;
  text-decoration: none;
  position: relative;
  color: #023178;
  border-color: #023178;
}

.c-btn-slide::before {
  content: "";
  width: 0;
  height: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.2s;
}

.c-btn-slide::after {
  content: "＞";
  font-size: 1.1em;
  position: absolute;
  right: 0.9em;
  top: 50%;
  transform: translateY(-50%);
  letter-spacing: 0;
  transition: 0.2s;
}

.c-btn-slide:hover::before {
  width: 100%;
}

.c-hamburger {
  display: block;
  width: 3.75rem;
  height: 3.75rem;
  position: relative;
  border-radius: 6.25rem;
  color: gray;
  background-color: red;
  z-index: 60;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger div {
  height: 0.125rem;
  width: 2.6875rem;
  border-radius: 5px; /* 線幅の半分 */
  background-color: white;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.3s;
}

.c-hamburger div:nth-of-type(1) {
  top: 43%;
  width: 1.75rem;
}

.c-hamburger div:nth-of-type(2) {
  top: 61%;
  width: 1.5rem;
  transform: translate(-50%, -50%);
}

.c-hamburger.active div:nth-of-type(1) {
  transform: rotate(45deg) translate(-50%, -50%);
  transform-origin: 0% 50%;
  top: 50%;
  left: 50%;
  width: 1.5rem;
}

.c-hamburger.active div:nth-of-type(2) {
  transform: rotate(-45deg) translate(-50%, -50%);
  transform-origin: 0% 50%;
  top: 50%;
  left: 50%;
  width: 1.5rem;
}

.c-hamburger .close,
.c-hamburger.active .open {
  opacity: 0;
}

.c-logo {
  width: 3.75rem;
}

@media screen and (min-width: 768px) {
  .c-logo {
    width: 5rem;
  }
}
.prev-next {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding-top: 1.5rem;
}
@media screen and (min-width: 768px) {
