}

.previous,
.next {
  color: red;
}

.previous a,
.next a {
  padding: 0 0.75rem 0.5rem;
  transition: all 0.3s;
  display: block;
}

.previous a:hover,
.next a:hover {
  opacity: 0.7;
  text-decoration: underline;
}

.top-link {
  display: block;
  transition: all 0.3s;
  font-size: 0.875rem;
}

